<script>
import { NButton } from 'naive-ui'
export default {
  name: "HomeView",
  data() {
    return {
      screenWidth: window.innerWidth,
    };
  },
  components: { NButton },
  beforeMount() {
    this.onResize()
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },
  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
